type ProductSizeListTypes = {
	sizeList: {
		name: string;
		id: number;
	}[];
	sizeLabel: string;
	currentSize: string;
	breadcrumbs?: {
		name: string;
		id: number;
		code: string;
	}[];
	refImage?: React.RefObject<HTMLDivElement>;
	chatRef: React.RefObject<HTMLDivElement>;
};

const ProductSizeList = ({ sizeList, sizeLabel, currentSize, refImage, chatRef, breadcrumbs }: ProductSizeListTypes) => {
	const [isOpenDetailInfo, setIsOpenDetailInfo] = React.useState<boolean>(false);
	const [currentSizeState, setCurrentSize] = React.useState<string>(currentSize);

	const colRef = React.useRef<HTMLDivElement | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const { t } = useTranslation();

	const onClickSizeHandler = (event: React.MouseEvent<HTMLDivElement>) => {
		const target = event.currentTarget;

		setCurrentSize(target.id);
	};

	const isOpenSizeGidHandler = () => {
		setIsOpenDetailInfo(true);
		if (colRef && colRef.current) colRef.current.style.zIndex = '11';
		if (refImage && refImage.current) refImage.current.style.zIndex = '0';
		if (chatRef && chatRef.current) chatRef.current.style.zIndex = '1';
	};

	const onCloseInfoHandler = () => {
		setIsOpenDetailInfo(false);
		if (colRef && colRef.current) colRef.current.style.zIndex = '10';
		if (refImage && refImage.current) refImage.current.style.zIndex = '10000';
		if (chatRef && chatRef.current) chatRef.current.style.zIndex = '100000';
	};

	React.useEffect(() => {
		setCurrentSize(currentSize);
	}, [currentSize]);

	return (
		<Col colRef={colRef} className={styles.sizeListCol} marginBottom={24}>
			<div className={styles.sizeList}>
				<p>{t('product:size')}</p>
				<div className={styles.sizes}>
					<div>
						{sizeList.map(item => {
							if (!item.name) return;
							return (
								<Link key={item.id} href={routes.productCard + item.id}>
									<div
										style={item.name === currentSizeState ? { borderColor: '#1B2530' } : {}}
										className={`${styles.size} ${SuisseIntl.className}`}
										onClick={onClickSizeHandler}
										id={item.name}
									>
										{item.name}
									</div>
								</Link>
							);
						})}
					</div>
					{/*       ,    */}
					{breadcrumbs[0].code !== 'muzhchiny' && (
						<>
							<button onClick={isOpenSizeGidHandler} className={styles.sizeBtn}>
								{t('product:sizeGuide')}
							</button>
							<DetailSidebarInfo onCloseInfo={onCloseInfoHandler} isOpen={isOpenDetailInfo}>
								<>
									<Title withSuisse={isMobile} size={isMobile ? 22 : 28} text={t('product:sizeGuide')} />
									<Col>
										<div>
											{SizeGidData.filter(
												item =>
													// 'golovnye_ubory','perchatki_i_varezhki',
													// refactor!!!!!!!!!!!!
													(item?.code === breadcrumbs[0]?.code &&
														breadcrumbs[2]?.code !== 'golovnye_ubory' &&
														breadcrumbs[2]?.code !== 'perchatki_i_varezhki') ||
													(item?.code === '' && item?.path === breadcrumbs[2]?.code)
											).map(item => {
												return (
													<div key={item.name} className={styles.sizeInfo}>
														<p>{item.name}</p>
														<div className={styles.columns}>
															{item.columns.map(itemCol => (
																<p key={itemCol}>{itemCol}</p>
															))}
														</div>
														{Object.keys(item).map(itemSize => {
															if (
																itemSize === 'name' ||
																itemSize === 'columns' ||
																itemSize === 'path' ||
																itemSize === 'code'
															) {
																return;
															}
															return (
																<div className={styles.sizeListVal} key={itemSize}>
																	<p>{itemSize}</p>
																	{item[itemSize].map(itemSizeVal => (
																		<p key={itemSize + itemSizeVal}>{itemSizeVal}</p>
																	))}
																</div>
															);
														})}
													</div>
												);
											})}
											{breadcrumbs[0].code === 'deti' && breadcrumbs[2]?.code === 'golovnye_ubory' && (
												<div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
													<Image
														src={'/static/img/instrNoBorder.png'}
														alt=""
														width={320}
														height={280}
														quality={100}
														className={styles.transp_img}
													/>
													{/* <Image
														src={'/static/img/headSizes.png'}
														alt=""
														width={191}
														height={250}
														quality={100}
													/> */}
												</div>
											)}
											{/* {breadcrumbs[0].code === 'deti' &&
												breadcrumbs[2]?.code === 'perchatki_i_varezhki' && (
													<Image
														src={'/static/img/glovesSizes.png'}
														alt=""
														width={261}
														height={138}
														quality={80}
													/>
												)} */}
										</div>
									</Col>
								</>
							</DetailSidebarInfo>
						</>
					)}
				</div>
				{sizeLabel && (
					<div className={styles.sizeLabel}>
						<p>{sizeLabel}</p>
					</div>
				)}
			</div>
		</Col>
	);
};

export default React.memo(ProductSizeList);
